  if (document.querySelector('.searching-results-main')) {
    const storiesPagination = () => {
      const content = document.querySelector('.searching-results-main-list');
      let itemsPerPage = 6;
      let currentPage = 0;
      const items = Array.from(content.querySelectorAll('.searching-results-main-item'));

      const showPage = page => {
        const startIndex = page * itemsPerPage;
        const endIndex = startIndex + itemsPerPage;
        items.forEach((item, index) => {
          item.classList.toggle('hidden', index < startIndex || index >= endIndex);
        })
        updateActiveButtonStates();
      }

      const createPageButton = () => {
        const totalPages = Math.ceil(items.length / itemsPerPage);
        const paginationContainer = document.querySelector('.searching-results-main-pagination');

        for (let i = 0; i < totalPages; i++) {
          const pageButton = document.createElement('button');
          pageButton.classList.add('btn-reset');
          pageButton.classList.add('searching-results-main-pagination__btn');
          pageButton.textContent = i + 1;
          pageButton.addEventListener('click', () => {
            currentPage = i;
            showPage(currentPage);
            updateActiveButtonStates();
          })
          paginationContainer.appendChild(pageButton);
        }

        const createPageButtonNext = () => {
          const pageButtonNext = document.createElement('button');
          pageButtonNext.textContent = '>';
          pageButtonNext.classList.add('btn-reset');
          pageButtonNext.classList.add('searching-results-main-pagination__btn');
          paginationContainer.appendChild(pageButtonNext);

          pageButtonNext.addEventListener('click', () => {
            currentPage++;
            showPage(currentPage);
            updateActiveButtonStates();
          })
        }
        createPageButtonNext();

        const btnShowMore = document.querySelector('.searching-results-main__show-more');
        btnShowMore.addEventListener('click', () => {
          itemsPerPage += 9;
          showPage(currentPage);
          updateActiveButtonStates();
        })
      }

      const updateActiveButtonStates = () => {
        const pageButtons = document.querySelectorAll('.searching-results-main-pagination__btn');
        pageButtons.forEach((button, index) => {
          if (index === currentPage) {
            button.classList.add('is-active');
          } else {
            button.classList.remove('is-active');
          }
        })
      }

      createPageButton();
      showPage(currentPage);
    }
    storiesPagination();

    const checkResoultSearching = () => {
      const searchResultMain = document.querySelector('.searching-results-main');
      let searchingList = document.querySelector('.searching-results-main-list');

      if (searchingList.querySelectorAll('.searching-results-main-item').length === 0) {
        searchResultMain.querySelector('.searching-results-main__result').innerText = 'Извините, по вашему запросу ничего не найдено. Попробуйте изменить ключевое слово.';
        searchResultMain.classList.add('is-hidden');
      } else {
        searchResultMain.querySelector('.searching-results-main__result').innerHTML = `
          По вашему запросу найдено
          <span class="searching-results-main__result_green">282 совпадения</span>
        `;
        searchResultMain.classList.remove('is-hidden');
      }
    }
    checkResoultSearching();
  }

